import * as ActionTypes from './actionTypes';

export const drawer = (state = { isOpen: false }, action) => {
    switch (action.type) {
        case ActionTypes.DRAWER_OPEN:
            return { ...state, isOpen: true };
        case ActionTypes.DRAWER_CLOSE:
            return { ...state, isOpen: false };
        default:
            return state;
    }
};
