import React, { Component } from 'react'
import { useLocation, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import About from './about'
import Education from './education'
import Employement from './employement'
import Header from './header'
import Home from './home'
import Skills from './skills'
import Summary from './summary'
import { drawerOpen, drawerClose } from '../redux/actionCreator'
import DrawerComponent from './drawer';
import Certification from './certifications';
import HomeScrollEffect from './HomeScrollEffect';
import { Card, Container, Grid, Link, Paper, Typography } from '@mui/material';
const mapStateToProps = state => {
    return {
        drawer: state.drawer
    };

}

const mapDispatchToProps = dispatch => ({
    openDrawer: () => dispatch(drawerOpen()),
    closeDrawer: () => dispatch(drawerClose())
})

function withRouter(Child) {
    return (props) => {
        const location = useLocation();
        const navigate = useNavigate();
        return <Child {...props} navigate={navigate} location={location} />;
    }
}
class Main extends Component {
    componentDidMount() {
        document.title = 'Swetabh Suman'
    }
    componentDidUpdate() {
        document.title = 'Swetabh Suman'
    }
    render() {
        const data = [{
            title: "Cross-Site Request Forgery (CSRF)",
            link: "https://medium.com/@swtbhsmn/cross-site-request-forgery-csrf-9e5f947f9854"
        },
        {
            title: "REST and RESTful APIs",
            link: "https://medium.com/@swtbhsmn/understanding-the-difference-between-rest-and-restful-apis-ecc752118e6d"
        }, {
            title: "Cookies",
            link: "https://medium.com/@swtbhsmn/exploring-the-power-of-web-storage-a-deep-dive-into-cookies-c117c7ed7d6e"
        }, {
            title: "Git cherry pick",
            link: "https://medium.com/@swtbhsmn/git-command-to-copy-commits-from-one-branch-to-another-e275b3458235"
        }
        ]
        return (
            <React.Fragment>
                <HomeScrollEffect {...this.props} />
                <Header props={this.props} />
                <Home />
                <Summary />
                <About />
                <Education />
                <Employement />
                <Skills />
                <Certification />
                <DrawerComponent props={this.props} />
                <Container sx={{ mt: "-100px" }}>
                    <Typography component={'h2'} sx={{p:2}} textAlign={'center'}>Blog on Medium</Typography>
                    <Grid container spacing={2} sx={{mb:"20px"}}>
                        {data.map((item, index) => {
                            return (
                                <Grid item key={index} xs={12} md={4}  >
                                    <Paper sx={{ height: "200px", display: "flex", justifyContent: "center", alignItems: "center" }} >
                                        <Link href={item.link} target={'_blank'}>{item.title}</Link>
                                    </Paper>
                                </Grid>
                            )
                        })
                        }
                    </Grid>
                </Container>
            </React.Fragment>
        )
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));